import React from 'react';

import '../styles/styles.css';

function MobileHomePage() {
    return (
        <main>
            <figure class="mobile-img">
                <img class="mobile-img" src={`${process.env.PUBLIC_URL}/images/zack_full_stack_dev.jpg`} />
                <figcaption>Hi! I'm Zack a Full Stack Developer! Check out my resume <span class="res-span"><a class ="res-link-mobile"  href="zachary_eggert_resume.pdf">here</a></span>!</figcaption>
            </figure>
        </main>
    )
}

export default MobileHomePage;